body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

h1 {
  font-size: 5rem;
  margin: 0;
}

* {
  box-sizing: border-box;
  font-size: 1rem;
  font-weight: 400;
  font-family: 'Roboto', sans-serif;
}

#root {
  width: 100vw;
  height: 100vh;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

h2 {
  font-size: 3rem;
  text-align: center;
  margin-bottom: 1rem;
}

img {
  max-width: 100%;
}